import { Link } from "react-router-dom";
import { Book } from "../../../../routes/book";
import { Main as BaseMain } from "../../../ui";
import classes from "./style.module.css";

export const Main = () => {
  return (
    <div className={classes.wrap}>
      <BaseMain
        className={classes.main}
        title="Письмо отправлено"
        titleImg="email.svg"
        titleImgAlt="email"
        desc="На указанный вами e-mail было отправлено письмо для смены пароля"
      >
        <Link className={classes.backToRoot} to={Book.root}>
          Вернуться на главную
        </Link>
      </BaseMain>
    </div>
  );
};
